*{
  margin: 0px;
}

body{
  background-color: #eee;
}

.header nav {
  background-color: black;
  height: 80px;
  color: white;
  display: flex;
  align-items: center;
  padding-right: 25px;
  padding-left: 25px;
}

.header nav a{
  color: white;
  text-decoration: none;
  margin-right: 10px;
  margin-left: 10px;
}

.header ul{
  flex-basis: 80%;
  display: flex;
  list-style: none;
  justify-content: flex-end;
}

.header nav a:hover{
  color: lightblue;
}
.activado{
  font-weight: bold;
  font-size: 18;
  color: rgb(13, 73, 111);
  text-decoration: none;
}

.content{
  width: 70%;
  min-height: 350px;
  background-color: white;
  border-radius: 15px;
  border: 1px solid #ccc;
  margin: 25px auto;
  padding: 25px;
}

.login input {
  display: block;
  margin: 15px;
  padding: 15px;
}